/* ========= Base Reset & Root Theme ========= */
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --bg:#000;
  --panel:#121212;
  --panel-alt:#181818;
  --panel-border:#2a2a2a;
  --panel-border-strong:#3a3a3a;
  --text:#f5f5f5;
  --text-dim:#b5b5b5;
  --accent:#d50000;
  --accent-glow:#ff3333;
  --accent-green:#108a32;
  --accent-green-hover:#0d6e28;
  --focus:#1e88e5;
  --warn:#ffb300;
  --danger:#e53935;
  --success:#1fa64b;
  --radius-sm:4px;
  --radius:12px;
  --radius-lg:26px;
  --shadow-sm:0 1px 2px rgba(0,0,0,.35);
  --shadow:0 4px 14px -2px rgba(0,0,0,.6),0 2px 4px rgba(0,0,0,.4);
  --transition:150ms cubic-bezier(.4,0,.2,1);
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

html, body {
  margin:0;
  padding:0;
  background:var(--bg);
  color:var(--text);
  -webkit-font-smoothing:antialiased;
  text-rendering:optimizeLegibility;
  min-height:100%;
}

body {
  font-size:16px;
  line-height:1.4;
}

/* ========= Utility ========= */
.hidden { display:none !important; }
.text-center { text-align:center; }
.mt-2 { margin-top:.5rem; }
.mt-4 { margin-top:1rem; }
.mb-2 { margin-bottom:.5rem; }
.mb-4 { margin-bottom:1rem; }
.mx-auto { margin-left:auto; margin-right:auto; }
.w-full { width:100%; }

/* ========= App Layout ========= */
.app-container {
  max-width:860px;
  margin:0 auto;
  padding:2.25rem 1.25rem 4rem;
}

@media (min-width:600px){
  .app-container { padding:3rem 2rem 5rem; }
}

.app-header {
  text-align:center;
  margin-bottom:1.75rem;
}

.logo {
  width:200px;
  max-width:65%;
  display:block;
  margin:0 auto 1.25rem;
  filter:drop-shadow(0 4px 8px rgba(0,0,0,.75));
  user-select:none;
}

@media (min-width:600px){
  .logo { width:260px; }
}

h1, h2, h3 {
  font-weight:700;
  line-height:1.15;
  letter-spacing:.5px;
  margin:0 0 .75rem;
}

h1 {
  font-size:clamp(2.15rem, 5vw, 3rem);
  text-shadow:0 2px 4px rgba(0,0,0,.6);
}

h2 {
  font-size:1.35rem;
  margin-top:1.25rem;
}

p { margin:.4rem 0; }

/* ========= Cards / Panels ========= */
.card,
.panel,
.user-panel,
.admin-panel,
.leader-banner {
  background:linear-gradient(145deg,#121212,#181818 55%);
  border:1px solid var(--panel-border);
  border-radius:var(--radius-lg);
  padding:1.2rem 1.4rem 1.4rem;
  box-shadow:var(--shadow);
  position:relative;
  overflow:hidden;
}

.card + .card,
.panel + .panel,
.user-panel + .user-panel,
.admin-panel + .admin-panel {
  margin-top:1.25rem;
}

.leader-banner {
  display:flex;
  align-items:center;
  justify-content:center;
  font-weight:500;
  gap:.6rem;
  margin:0 auto 1.4rem;
  padding:.9rem 1.1rem;
  border:1px solid var(--panel-border-strong);
  background:linear-gradient(100deg,#181818,#202020 55%);
  max-width:680px;
  font-size:.95rem;
  border-radius:12px;
}

.leader-banner .emoji {
  font-size:1.1rem;
  animation:float 3.2s ease-in-out infinite;
}

@keyframes float {
  0%,100% { transform:translateY(-1px); }
  50% { transform:translateY(2px); }
}

/* ========= Lists ========= */
ul {
  list-style:none;
  padding-left:0;
  margin:.75rem 0 0;
}

.coupon-item {
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:.75rem;
  padding:.55rem .85rem;
  background:#141414;
  border:1px solid #242424;
  border-radius:var(--radius);
  font-size:.92rem;
  margin-bottom:.55rem;
  transition:background var(--transition), border-color var(--transition);
}

.coupon-item:last-child { margin-bottom:0; }

.coupon-item.redeemed {
  opacity:.55;
  filter:saturate(.5);
}

.coupon-badge {
  font-size:1.1rem;
  margin-right:.35rem;
}

.status-pill {
  display:inline-flex;
  align-items:center;
  gap:.45rem;
  font-size:.8rem;
  padding:.25rem .6rem;
  border-radius:999px;
  background:#151515;
  border:1px solid #272727;
  letter-spacing:.5px;
}

.status-pill.green { color:var(--success); }
.status-pill.red { color:var(--danger); }

.status-pill .dot {
  width:.5rem;
  height:.5rem;
  border-radius:50%;
  background:currentColor;
  box-shadow:0 0 6px 2px currentColor;
}

/* ========= Buttons ========= */
.btn {
  --btn-bg:#262626;
  --btn-bg-hover:#333;
  --btn-bg-active:#2b2b2b;
  --btn-color:var(--text);
  border:none;
  outline:none;
  background:var(--btn-bg);
  color:var(--btn-color);
  font-weight:600;
  letter-spacing:.4px;
  padding:.72rem 1.35rem;
  font-size:.95rem;
  line-height:1;
  border-radius:14px;
  cursor:pointer;
  display:inline-flex;
  align-items:center;
  justify-content:center;
  gap:.5rem;
  position:relative;
  transition:background var(--transition), transform var(--transition), box-shadow var(--transition);
  box-shadow:0 2px 6px -1px rgba(0,0,0,.55),0 2px 3px -1px rgba(0,0,0,.5);
  user-select:none;
}

.btn:hover {
  background:var(--btn-bg-hover);
}

.btn:active {
  background:var(--btn-bg-active);
  transform:translateY(1px);
}

.btn:focus-visible {
  outline:2px solid var(--focus);
  outline-offset:2px;
}

.btn.primary {
  --btn-bg:var(--accent-green);
  --btn-bg-hover:var(--accent-green-hover);
  --btn-bg-active:#0a5a1e;
  --btn-color:#fff;
}

.btn.danger {
  --btn-bg:#b00020;
  --btn-bg-hover:#c00026;
  --btn-bg-active:#8c001a;
}

.btn.outline {
  --btn-bg:transparent;
  border:1px solid #3a3a3a;
}

.btn.outline:hover {
  background:#1f1f1f;
}

.btn.small {
  padding:.45rem .85rem;
  font-size:.75rem;
  font-weight:500;
  border-radius:10px;
}

.btn.block {
  width:100%;
}

.btn[disabled],
.btn.disabled {
  opacity:.55;
  cursor:not-allowed;
  filter:grayscale(.4);
}

/* Glimmer for primary CTAs */
.btn.primary:not(.disabled)::after {
  content:"";
  position:absolute;
  inset:0;
  background:
    linear-gradient(120deg,rgba(255,255,255,.0) 0%,rgba(255,255,255,.18) 42%,rgba(255,255,255,.0) 60%);
  opacity:0;
  transform:translateX(-25%);
  transition:opacity 400ms ease, transform 1200ms ease;
  pointer-events:none;
  mix-blend-mode:overlay;
}

.btn.primary:hover::after {
  opacity:1;
  transform:translateX(45%);
}

/* ========= Forms ========= */
input[type="text"],
input[type="email"],
input[type="password"] {
  width:100%;
  background:#121212;
  color:var(--text);
  border:1px solid #303030;
  border-radius:12px;
  padding:.75rem 1rem;
  font-size:.95rem;
  letter-spacing:.4px;
  box-shadow:var(--shadow-sm);
  transition:border-color var(--transition), background var(--transition), box-shadow var(--transition);
}

input:focus {
  outline:none;
  border-color:var(--focus);
  background:#151515;
  box-shadow:0 0 0 3px rgba(30,136,229,.3);
}

input::placeholder {
  color:#5a5a5a;
}

/* ========= Admin Users panel items ========= */
.admin-user {
  padding:.75rem 1rem;
  margin:.6rem 0;
  border:1px solid #2b2b2b;
  border-radius:var(--radius);
  background:#141414;
  display:flex;
  flex-direction:column;
  gap:.35rem;
  font-size:.86rem;
}

.admin-user strong {
  font-weight:600;
}

/* ========= Modal & Scanner ========= */
.modal-backdrop {
  position:fixed;
  inset:0;
  background:rgba(0,0,0,.72);
  display:flex;
  align-items:center;
  justify-content:center;
  padding:1.25rem;
  z-index:2000;
  backdrop-filter:blur(4px);
  -webkit-backdrop-filter:blur(4px);
  animation:fadeIn .35s ease;
}

@keyframes fadeIn {
  from { opacity:0; }
  to { opacity:1; }
}

.modal {
  width:100%;
  max-width:680px;
  background:linear-gradient(155deg,#141414,#1a1a1a 60%);
  border:1px solid #252525;
  border-radius:40px;
  padding:2.4rem 2.1rem 2.6rem;
  position:relative;
  box-shadow:0 10px 38px -12px rgba(0,0,0,.75), 0 4px 18px -6px rgba(0,0,0,.55);
  animation:popIn .4s cubic-bezier(.4,0,.25,1);
  overflow:hidden;
}

@media (max-width:640px){
  .modal {
    border-radius:30px;
    padding:2.1rem 1.6rem 2.3rem;
  }
}

@keyframes popIn {
  0% { transform:translateY(18px) scale(.95); opacity:0; }
  55% { transform:translateY(-4px) scale(1.012); opacity:1; }
  100% { transform:translateY(0) scale(1); }
}

.modal h2 {
  margin:0 0 .85rem;
  font-size:clamp(1.75rem,4.4vw,2.35rem);
  text-align:center;
  letter-spacing:.7px;
}

.modal p.desc {
  margin:.25rem 0 1.25rem;
  text-align:center;
  color:var(--text-dim);
  font-size:.97rem;
  letter-spacing:.45px;
  line-height:1.4;
}

.scan-inner {
  width:100%;
  max-width:700px;
  margin:0 auto;
  display:flex;
  flex-direction:column;
  align-items:center;
}

.scan-frame {
  width:72vw;
  height:72vw;
  max-width:560px;
  max-height:560px;
  border-radius:46px;
  position:relative;
  background:#0c0c0c;
  display:flex;
  align-items:center;
  justify-content:center;
  margin:0 auto 1.6rem;
  border:3px solid #1f1f1f;
  box-shadow:
    inset 0 0 0 3px rgba(255,255,255,.03),
    0 0 0 1px #2d2d2d,
    0 6px 20px -8px rgba(0,0,0,.8),
    0 2px 8px -2px rgba(0,0,0,.65),
    0 0 0 12px rgba(213,0,0,.06);
  overflow:hidden;
  animation:scannerPulse 3.5s ease-in-out infinite;
}

@keyframes scannerPulse {
  0%,100% { box-shadow:
    inset 0 0 0 3px rgba(255,255,255,.03),
    0 0 0 1px #2d2d2d,
    0 6px 20px -8px rgba(0,0,0,.8),
    0 2px 8px -2px rgba(0,0,0,.65),
    0 0 0 12px rgba(213,0,0,.05); }
  50% { box-shadow:
    inset 0 0 0 3px rgba(255,255,255,.04),
    0 0 0 1px #323232,
    0 6px 24px -8px rgba(0,0,0,.85),
    0 2px 8px -2px rgba(0,0,0,.7),
    0 0 0 14px rgba(213,0,0,.1); }
}

@media (min-width:480px){
  .scan-frame {
    width:420px;
    height:420px;
    border-radius:50px;
  }
}

.scan-frame::before {
  background: linear-gradient(125deg,#ff2d2d,#e00000 38%,#700000 85%);

  /* Add the standard property first */
  mask:
    linear-gradient(#000,#000) content-box,
    linear-gradient(#000,#000);
  mask-composite: exclude;

  /* Keep WebKit versions for iOS Safari */
  -webkit-mask:
    linear-gradient(#000,#000) content-box,
    linear-gradient(#000,#000);
  -webkit-mask-composite: xor;

  opacity:.8;
  pointer-events:none;
}

.scan-frame::after {
  --glow:rgba(255,65,65,.35);
  content:"";
  position:absolute;
  inset:0;
  background:
    radial-gradient(circle at 50% 15%,rgba(255,65,65,.15),transparent 60%),
    linear-gradient(to bottom,rgba(255,0,0,.08),transparent 60%);
  mix-blend-mode:plus-lighter;
  opacity:.4;
  pointer-events:none;
  animation:glowFloat 9s linear infinite;
}

@keyframes glowFloat {
  0% { transform:translateY(0); }
  50% { transform:translateY(5px); }
  100% { transform:translateY(0); }
}

.scan-video {
  width:100%;
  height:100%;
  object-fit:cover;
  border-radius:inherit;
  position:relative;
  z-index:2;
  filter:brightness(.9) contrast(1.12) saturate(1.08);
  background:#000;
  animation:videoIn .6s ease;
}

@keyframes videoIn {
  from { opacity:0; transform:scale(1.04); }
  to { opacity:1; transform:scale(1); }
}

.scan-status {
  position:absolute;
  bottom:-2.5rem;
  left:0;
  right:0;
  text-align:center;
  font-size:.95rem;
  color:var(--text-dim);
  letter-spacing:.5px;
  font-weight:500;
  animation:fadeSlide .55s ease;
}

@keyframes fadeSlide {
  from { opacity:0; transform:translateY(4px); }
  to { opacity:1; transform:translateY(0); }
}

.modal-actions {
  margin-top:.25rem;
  display:flex;
  justify-content:center;
  gap:1.1rem;
  flex-wrap:wrap;
}

.help-text {
  font-size:.72rem;
  color:#666;
  text-align:center;
  line-height:1.4;
  margin-top:1.1rem;
  letter-spacing:.4px;
}

.cooldown {
  margin-top:.9rem;
  text-align:center;
  color:var(--warn);
  font-size:.85rem;
  animation:fadeIn .4s ease;
}

/* ========= Animations / Feedback ========= */
@keyframes ping {
  0% { transform:scale(.75); opacity:.6; }
  50% { transform:scale(1); opacity:1; }
  100% { transform:scale(.75); opacity:.6; }
}

.ping { animation:ping 1.8s ease-in-out infinite; }

/* ========= Scrollbars (optional) ========= */
::-webkit-scrollbar {
  width:10px;
}
::-webkit-scrollbar-track {
  background:#0a0a0a;
}
::-webkit-scrollbar-thumb {
  background:#272727;
  border-radius:20px;
}
::-webkit-scrollbar-thumb:hover {
  background:#333;
}

/* ========= Media Tweaks ========= */
@media (max-width:480px){
  .leader-banner { font-size:.85rem; padding:.75rem .85rem; }
  .btn { padding:.68rem 1.1rem; font-size:.9rem; }
  .modal { padding:2rem 1.25rem 2.4rem; }
  .modal h2 { font-size:1.95rem; }
  .scan-status { font-size:.85rem; }
}

/* ========= Selection ========= */
::selection {
  background:#ff1744;
  color:#fff;
}
